.dd{
	.accordion-button:not(.collapsed){
		box-shadow:unset;
	}
	.accordion-body{
		border: 1px solid $border-color;
		border-bottom-left-radius: 0.5rem;
		border-bottom-right-radius: 0.5rem;
		border-top: 0;
		//margin-top: -5px;
	}
	.dd-list{
		@include respond ('phone'){
			padding-left:0;
		}
	}
}
.collapse{
	border-radius:$radius;
}
.publish-drop{
	ul{
		&:hover{
			background-color:unset;
		}
	}
}
 input.form-control{
	position:relative;
}
input[type="date"]::-webkit-calendar-picker-indicator {
    background: transparent;
    bottom: 0;
    color: transparent;
    cursor: pointer;
    height: auto;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: auto;
}
.right-sidebar-sticky{
	position:sticky;
	top:7.5rem;
}	
.content-title{
	&.box-primary,
	&.box-secondary,
	&.box-success,
	&.box-danger,
	&.box-warning,
	&.box-info,
	&.box-dark,
	&.box-light{
		background-color:var(--primary);
		.cpa,
		i{
			color:$white!important;
			
		}
	}
	&.box-secondary{
		background-color:var(--secondary);
	}
	&.box-success{
		background-color:$success;
	}
	&.box-danger{
		background-color:$danger;
	}
	&.box-warning{
		background-color:$warning;
	}
	&.box-info{
		background-color:$info;
	}
	&.box-dark{
		background-color:$dark;
	}
	&.box-light{
		background-color:$light;
	}
}

.list-style-1{
	&.block{
		& > li{
			display: block;
		}
	}
	& > li{
		border-bottom: 1px dashed var(--border);
		padding: .75rem 0;
		display: flex;
		align-items: center;
	}
}

.rounded-sm{
	border-radius: 0.375rem;
}

.accord-data {
	.menu-accord {
		.accordion-button{
			padding-left: 4.1rem;
			box-shadow: none;
			background: #fff;
		}
	}
	.menu-ac-item {
		.nestable-list.nestable-group{
			padding-left: 0;
		}
		.accordion-body{
			border: 1px solid #dbdde6;
			border-bottom-left-radius: 0.5rem;
  			border-bottom-right-radius: 0.5rem;
  			border-top: 0;
			margin-top: -5px;
		}
	}
}